.contact__list {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  border: 1px solid rgb(91, 7, 94);
  box-shadow: -5px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
  color: blue;
  font-weight: bold;
  justify-content: space-between;
}
.contact__item {
  margin-left: auto;
}
